.app-wrapper {
  margin: 0 auto;
  max-width: 1200px;
  display: grid;
  grid-template-areas: "h h" "n m";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  /* grid-gap: 10px; */
}
.app-wrapper a {
  text-decoration: none;
}
.app-wrapper-content {
  grid-area: m;
  background-color: cornflowerblue;
}
